import * as React from 'react';
import UtilPage from '@splunk/react-docs/UtilPage';

// https://github.com/jsdoc2md/jsdoc-api#synopsis
interface JsDoc {
    access?: string;
    classdesc?: string;
    comment: string;
    kind: string;
    longname: string;
    meta: Record<string, unknown>;
    member: string;
    name: string;
    scope: string;
}

const sharedSelectors = ['getField', 'min', 'max'];

const formatSidebarItemName = (name: string, className: string): string => {
    return sharedSelectors.find(sharedSelector => name === sharedSelector) ? `${className}#${name}` : name;
};

const removePrependedClassName = (longname = ''): string => longname.slice(longname.indexOf('#') + 1);

const formatDSLSelectorDocs = (docs: JsDoc[]): JsDoc[] =>
    docs.map(doc => ({
        ...doc,
        longname: removePrependedClassName(doc.longname),
    }));

export const getFormatterClassdesc = (
    formatterDocs: JsDoc[],
    name: string
): { name: string; markdown: string } => ({
    name,
    markdown: `# ${name}\n ${formatterDocs.find(doc => doc.name === name && doc.kind === 'class').classdesc}`,
});

export const extractDocItems = (
    docs: JsDoc[],
    className: string
): { name: string; component: (...args: any[]) => any }[] => {
    const classHeaderDoc = docs[0];
    const fileDoc = docs[docs.length - 1];
    const filteredDocs = formatDSLSelectorDocs(docs).filter(
        ({ access, comment }) => access === 'public' && comment
    );

    return filteredDocs
        .map(doc => ({
            name: formatSidebarItemName(doc.name, className),
            component: function UtilDoc() {
                // named function so linter doesnt raise react/display-name error
                return (
                    <UtilPage
                        title={`${className}#${doc.name}`}
                        status={1}
                        docs={[classHeaderDoc, doc, fileDoc]}
                    />
                );
            },
        }))
        .sort((a, b) => a.name.localeCompare(b.name));
};
